import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";
import { faSquareArrowUpRight } from "@fortawesome/free-solid-svg-icons";
import Pill from "./Pill";
export default function ProjectCard({ project }) {
  return (
    <div className="w-full mb-6 mx-auto bg-white rounded-lg overflow-hidden shadow-md flex">
      <div className="w-1/2 flex items-center justify-center">
        <img
          src={project.image}
          alt={project.title}
          className="w-full h-auto"
        />
      </div>
      <div className="w-1/2 p-4 flex flex-col justify-between">
        <div>
          <div className="flex justify-between">
            <h3 className="text-xl font-bold text-gray-800">{project.title}</h3>
            <span>
              <a
                href={project.github}
                target="_blank"
                rel="noopener noreferrer"
                className="inline text-bold bg-gray-800 text-sm text-white rounded-3xl shadow-lg px-2 py-1 hover:bg-gray-600"
              >
                <FontAwesomeIcon icon={faGithub} className="mr-2" />
                GitHub
              </a>
              {project.hosting && (
                <a
                  href={project.hosting}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="inline text-bold ml-3 bg-gray-800 text-sm text-white rounded-3xl shadow-lg px-2 py-1 hover:bg-gray-600"
                >
                  <FontAwesomeIcon
                    icon={faSquareArrowUpRight}
                    className="mr-2"
                  />
                  Visit
                </a>
              )}
            </span>
          </div>
          <p className="text-gray-600 mt-4 h-32">{project.summary}</p>
          <div className="mt-2 grid grid-cols-4 gap-1">
            
              {project.technologies.map((tech) => (
                <div className="col-span-1">
                  <Pill text={tech} />
                </div>
              ))}
         
          </div>
        </div>
      </div>
    </div>
  );
}
